/**
* 法规库-执法领域组件
* @module 案件
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-from :onAdd="onAdd" :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                  @row-click="seeFunction" border v-loading="loading"
                  size="mini">
            <el-table-column align="center" label="编号" prop="pmNum"/>
            <el-table-column align="center" label="名称" prop="pmName"/>
            <el-table-column align="center" label="生效日期" width="150">
                <template slot-scope="scope">
                    {{scope.row.effectiveTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="状态" width="80" prop="isEffective">
                <template slot-scope="scope">
                    {{scope.row.isEffective | isEffective}}
                </template>
            </el-table-column>
            <el-table-column label="操作" width="180">
                <template slot-scope="scope">
                    <el-tooltip :open-delay="500" content="查看该事项历史版本" placement="top">
                        <el-button @click.stop="watchLog(scope.$index, scope.row)" plain size="mini"
                                   type="primary">历史版本
                        </el-button>
                    </el-tooltip>
                    <el-tooltip :open-delay="500" content="对当前项生成新版本" placement="top">
                        <el-button @click.stop="reviseFunction(scope.$index, scope.row)" plain size="mini"
                                   type="warning" v-if="buttonCheck('edit')">修订
                        </el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                       @getData="getList" class="page-css"/>
        <!-- 编辑&添加弹框 -->
        <change-edit :isRevise="editObj.isRevise" :lawId="lawId" :tid="tid" :show.sync="editObj.show" :title="editObj.title"
                     :type="editObj.editId" @getList="getList"/>
        <!-- 编辑&添加弹框 -->
        <log-list-dialog :id="logObj.id" :show.sync="logObj.show" @getList="getList"/>
        <!-- 查看 -->
        <see-content :editId='seeObj.editId' :show.sync='seeObj.show' :title='seeObj.title'></see-content>
    </div>
</template>

<script>
    import LjPagination from '../../../../components/LjPagination'
    import LjSearchFrom from '../../../../components/LjSearchForm'
    import {formatDate} from "../../../../until/wordBook";
    import changeEdit from "./editadd";
    import logListDialog from './logList'
    import seeContent from "./seeContent";

    import {apiPunishListSearch, apiPunishMatterDelete} from "../../../../api/case";

    export default {
        name: "punishPage",
        props: ['lawId','version','tid'],
        data() {
            return {
                loading:true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                logObj: {
                    show: false,
                    id: ''
                },
                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    isEffective: 1,
                    pageNum: 1
                },
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                total: 0,
                list: [],
            }
        },
        components: {
            changeEdit, LjPagination, LjSearchFrom, logListDialog, seeContent
        },


        methods: {
            //查看内容
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = '查看详情';
                this.seeObj.editId = row.tid
            },
            watchLog(index, data) {
                this.logObj.id = data.pmId;
                this.logObj.show = true
            },
            //查询+搜索
            getList() {
                this.form.lawId = this.lawId;
                this.form.lawVersion = this.version;
                apiPunishListSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    let arr = [];
                    res.data.forEach(i => {
                        i.name = i.pmName;
                        i.id = i.pmId;
                        i.isLeaf = true
                        arr.push(i)
                    });
                    this.$emit('upDateTree', arr);
                    this.loading=false;
                })
            },
            //新增
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = '新增处罚事项';
                this.editObj.show = true;
                this.editObj.isRevise = false
            },

            //编辑
            reviseFunction(index, row) {
                this.editObj.editId = row.tid;
                this.editObj.title = '修订处罚事项';
                this.editObj.show = true;
                this.editObj.isRevise = true
            },
            //删除按钮事件
            deleteFunction(index, data) {
                this.$confirm('此操作将永久删除该领域, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiPunishMatterDelete(data.tid).then(res => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                })
            },
            //搜索
            searchList(data) {  //data就是接收的this.form数据
                 this.loading = true;
                 data.isEffective=1;
                 data.lawId = this.lawId;
                 data.lawVersion = this.version;
                //  this.form = data;
                apiPunishListSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    let arr = [];
                    res.data.forEach(i => {
                        i.name = i.pmName;
                        i.id = i.pmId;
                        i.isLeaf = true;
                        arr.push(i)
                    });
                    this.$emit('upDateTree', arr);
                    this.loading=false;
                })
            },
        },
        filters: {
            formatDate(val) {
                return formatDate(val, 'yyyy-MM-dd')
            },
            isEffective(val){
                return val==0?"未生效":(val==1?"已生效":"")
            }
        },
        watch: {
            'lawId'(val, Oval) {
                if (val !== Oval) this.getList()
            }
        },
        created() {
            this.getList();
        },
    }
</script>

<style scoped>
    .wrop-search {
        margin-bottom: 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>
