/**
* 法规库-执法领域组件
* @module 案件
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" append-to-body
               width="750px">
        <div class="punish-block" v-if="form" v-loading="loading">
            <h3>{{form.pmName}}</h3>
            <div class="enforceCode"><span>处罚编号：{{form.pmNum}}</span></div>
            <el-divider content-position="left"><span class="divider-span">启用：{{form.isEffective | YESORNO}}</span>
            </el-divider>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="12">
                    <el-row>
                        <el-col :span="8"><label>所属领域：</label></el-col>
                        <el-col :span="16">{{form.enforceName}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="12">
                    <el-row>
                        <el-col :span="8"><label>法律法规：</label></el-col>
                        <el-col :span="16">{{form.lawName}}</el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label>违反条例：</label></el-col>
                        <el-col :span="20">{{form.violationRegulations}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label>依据条例：</label></el-col>
                        <el-col :span="20">{{form.accordingRegulations}}</el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label>违反条例内容：</label></el-col>
                        <el-col :span="20">{{form.violationRegulationsContent}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label>依据条例内容：</label></el-col>
                        <el-col :span="20">{{form.accordingRegulationsContent}}</el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label>职权依据：</label></el-col>
                        <el-col :span="20">{{form.rightclause}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="24">
                    <el-row>
                        <el-col :span="4"><label>处罚内容：</label></el-col>
                        <el-col :span="20">
                            <el-tag v-if="form.isWarning">警告</el-tag>
                            <el-tag v-if="form.isChange">责令改正</el-tag>
                            <el-tag v-if="form.isChangebefore">整改前置</el-tag>
                            <el-tag v-if="form.isFine">罚款</el-tag>
                            <el-tag v-if="form.isRevokeGoods">暂扣物品</el-tag>
                            <el-tag v-if="form.isConfiscate">没收</el-tag>
                            <el-tag v-if="form.isStop">停产停业</el-tag>
                            <el-tag v-if="form.isRevoke">暂扣或吊销许可证</el-tag>
                            <el-tag v-if="form.isForceDismantle">可以强拆</el-tag>
                            <el-tag v-if="form.isDedention">行政拘留</el-tag>
                            <el-tag v-if="form.isOtherPunish">其他处罚</el-tag>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="punish-row">
                <el-col :span="12">
                    <el-row>
                        <el-col :span="8"><label>自然人罚款范围：</label></el-col>
                        <el-col :span="16">{{form.naturalRangeMin}}-{{form.naturalRangeMax}}</el-col>
                    </el-row>
                </el-col>
                <el-col :span="12">
                    <el-row>
                        <el-col :span="8"><label>法人罚款范围：</label></el-col>
                        <el-col :span="16">{{form.legalRangeMin}}-{{form.legalRangeMax}}</el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-divider content-position="right" v-if="form.effectiveTime">
                <span
                        class="divider-span">生效时间：{{ form.effectiveTime | formatDate}}
                    <span v-if="form.invalidTime">至 {{ form.invalidTime | formatDate}}</span>
                </span>
            </el-divider>
        </div>
    </el-dialog>

</template>

<script>
    import {formatDate, YESORNO} from "../../../../until/wordBook";
    import {apiPunishMatterGet} from "../../../../api/case";

    export default {
        name: "seeContent",
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            },

        },
        data() {
            return {
                loading:true,
                form: null,
                enforceList: [],
                onlyShow: false,
                see: false
            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            //弹框弹出时获取data给form
            initPage() {
                if (this.editId) {
                    apiPunishMatterGet(this.editId).then(res => {
                        this.loading=false;
                        this.form = res.data;
                    })
                }
            }
        },
        filters: {
            YESORNO(val) {
                return YESORNO[val]
            },
            formatDate(val) {
                return formatDate(val, 'yyyy-MM-dd')
            }
        },
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },


    }
</script>

<style lang="less" scoped>
    .enforceCode {
        text-align: center;
        color: #aaaaaa;
        font-size:  @fontSize ;
        margin-top: 5px
    }

    .el-tag {
        margin: 0 3px;
    }

    .punish-block {
        h3 {
            text-align: center;
        }

        .divider-span {
            color: #cccccc;
        }

        .punish-row {
            line-height: 30px;

            label {
                display: inline-block;
                color: #8ca7b2;
                width: 120px;
                text-align: right;
            }
        }
    }

    .concss {
        font-weight: bold
    }
</style>
