/**
* 法规库-执法领域组件
* @module 案件
* @desc 历史版本
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' :visible="show" @close="closeDialog" center title="全部版本" width="900px">
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" @row-click="showFunction" border size="mini" v-loading="loading">
            <el-table-column align="center" label="名称" prop="pmName"/>
            <el-table-column align="center" label="生效日期">
                <template slot-scope="scope">
                    {{scope.row.effectiveTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="状态" width="80">
                <template slot-scope="scope">
                    {{scope.row.isEffective == 1 ? '已生效' : (scope.row.isEffective == 0 ? '已失效' : '待生效') }}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="80">
                <template slot-scope="scope">
                    <el-tooltip :open-delay="500" content="修改当前项" placement="top">
                        <el-button @click.stop="editFunction(scope.$index, scope.row)" plain size="mini"
                                   type="warning"
                                   v-if="scope.row.isEffective === 2">编辑
                        </el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <i-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                      @getData="getList" class="page-css"/>
        <!-- 编辑&添加弹框 -->
        <change-edit :isRevise="editObj.isRevise" :onlyShow="editObj.onlyShow" :see='see'
                     :show.sync="editObj.show" :title="editObj.title" :type="editObj.editId" @getList="getList"/>
        <!-- 查看 -->
        <see-content :editId='seeObj.editId' :show.sync='seeObj.show' :title='seeObj.title'></see-content>
    </el-dialog>
</template>

<script>
    import iPagination from '../../../../components/LjPagination'
    import searchForm from '../../../../components/LjSearchForm'
    import {formatDate} from "../../../../until/wordBook";
    import changeEdit from "./editadd";
    import seeContent from "./seeContent";

    import {apiPunishListSearch, apiPunishMatterDelete} from "../../../../api/case";

    export default {
        props: ['show', 'id'],
        data() {
            return {
                loading:true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1,
                },
                total: 0,
                list: [],
                see: false
            }
        },
        components: {
            changeEdit, iPagination, searchForm, seeContent
        },


        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            //查询+搜索
            getList() {
                apiPunishListSearch(this.form).then(res => {
                    this.loading=false;
                    this.total = res.total;
                    this.list = res.data;
                    this.$emit('getList')
                })
            },
            showFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = '查看全部';
                this.seeObj.editId = row.tid
            },
            editFunction(index, row) {
                this.editObj.editId = row.tid;
                this.editObj.title = '修改';
                this.editObj.onlyShow = false;
                this.editObj.isRevise = false
                this.editObj.show = true;
                this.see = false;

            },

            initPage() {
                this.form.pmId = this.id;
                this.getList()
            },
            //编辑
            reviseFunction(index, row) {
                this.editObj.editId = row.tid;
                this.editObj.title = '修订';
                this.editObj.show = true;
                this.editObj.isRevise = true
            },
            //删除按钮事件
            deleteFunction(index, data) {
                this.$confirm('此操作将永久删除该领域, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.loading=true;
                    apiPunishMatterDelete(data.tid).then(res => {
                        this.loading=false;
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                })
            },
        },
        filters: {
            formatDate(val) {
                return formatDate(val, 'yyyy-MM-dd')
            },
        },
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage()
                }
            }
        }
    }
</script>

<style scoped>
    .page-css {
        margin-top: 10px
    }
</style>
