/**
* 法规库-处罚事项组件
* @module 案件
* @desc 修改添加
* @author 王伟
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="title"
                :visible="show"
                @close="closeDialog"
                append-to-body
                center
                top="30px"
                width="800px"
        >
            <el-form
                    :model="form"
                    :rules="rules"
                    label-width="140px"
                    ref="ruleForm"
                    size="medium"
                    status-icon
                    v-loading="loading"
            >
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="所属领域：">{{form.enforceName}}</el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="法规：">{{form.lawName}}</el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="处罚编号：" v-if="type">
                            <span>{{form.pmNum}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="适用案件类型：" prop="caseUseType">
                            <el-select class="more-select" multiple placeholder="请选择案件类型" v-model="form.caseUseType">
                                <el-option
                                        :key="data.dictValue"
                                        :label="data.dictName"
                                        :value="data.dictValue"
                                        v-for="data in caseTypeList"
                                ></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="违法行为类型：">
                            <el-select @change="changeType" placeholder="请选择违法行为类型" v-model="form.illegalTypes">
                                <el-option
                                        :key="data.dictValue"
                                        :label="data.dictName"
                                        :value="data.dictValue"
                                        v-for="data in typeList"
                                ></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>

                    <el-col :span="24">
                        <el-form-item label="处罚名称：" prop="pmName">
                            <el-input :readonly="onlyShow" placeholder="请输入处罚名称" v-model="form.pmName"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="24">
                        <el-form-item label="违反条例：" prop="violationRegulations">
                            <el-input
                                    :readonly="onlyShow"
                                    placeholder="请输入违反条例"
                                    v-model="form.violationRegulations"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="违反条例内容：" prop="violationRegulationsContent">
                            <el-input
                                    :readonly="onlyShow"
                                    placeholder="请输入违反条例内容"
                                    type="textarea"
                                    v-model="form.violationRegulationsContent"
                            ></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="24">
                        <el-form-item label="依据条例：" prop="accordingRegulations">
                            <el-input
                                    :readonly="onlyShow"
                                    placeholder="请输入依据条例"
                                    v-model="form.accordingRegulations"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="依据条例内容：" prop="accordingRegulationsContent">
                            <el-input
                                    :readonly="onlyShow"
                                    placeholder="请输入依据条例内容"
                                    type="textarea"
                                    v-model="form.accordingRegulationsContent"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="职权依据：" prop="rightclause">
                            <el-input :readonly="onlyShow" placeholder="请输入职权依据" v-model="form.rightclause"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="自然人罚款下限：">
                            <el-input-number
                                    :disabled="onlyShow"
                                    :min="0"
                                    :step="50"
                                    placeholder="请输入编码"
                                    v-model="form.naturalRangeMin"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="自然人罚款上限：">
                            <el-input-number
                                    :disabled="onlyShow"
                                    :min="form.naturalRangeMin"
                                    :step="50"
                                    placeholder="请输入编码"
                                    v-model="form.naturalRangeMax"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法人罚款下限：">
                            <el-input-number
                                    :disabled="onlyShow"
                                    :min="0"
                                    :step="50"
                                    placeholder="请输入编码"
                                    v-model="form.legalRangeMin"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法人罚款上限：">
                            <el-input-number
                                    :disabled="onlyShow"
                                    :min="form.legalRangeMin"
                                    :step="50"
                                    placeholder="请输入编码"
                                    v-model="form.legalRangeMax"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="处罚内容：">
                            <fieldset>
                                <legend>一般处罚措施</legend>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isWarning"
                                >警告
                                </el-checkbox>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isChange"
                                >责令改正
                                </el-checkbox>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isChangebefore"
                                >整改前置
                                </el-checkbox>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isFine"
                                >罚款
                                </el-checkbox>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isRevoke"
                                >暂扣或吊销许可证（执照）
                                </el-checkbox>
                                <!-- <el-checkbox :disabled="onlyShow" :false-label="0" :true-label="1"
                                                            v-model="form.isOtherPunish">其他处罚
                                                </el-checkbox>
                                -->
                            </fieldset>
                            <fieldset>
                                <legend>强制处罚措施</legend>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isRevokeGoods"
                                >强制执行（暂扣）
                                </el-checkbox>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isForceDismantle"
                                >强制执行（拆除）
                                </el-checkbox>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isConfiscate"
                                >没收
                                </el-checkbox>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isStop"
                                >停产停业
                                </el-checkbox>

                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isDedention"
                                >行政拘留
                                </el-checkbox>
                                <el-checkbox
                                        :disabled="onlyShow"
                                        :false-label="0"
                                        :true-label="1"
                                        v-model="form.isOtherPunish"
                                >其他处罚
                                </el-checkbox>
                            </fieldset>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="是否立即生效：" v-if="type">
                            <el-switch
                                    :active-value="1"
                                    :disabled="onlyShow"
                                    :inactive-value="2"
                                    v-model="form.isEffective"
                            ></el-switch>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12" v-if="form.isEffective == 2">
                        <el-form-item label="生效时间：">
                            <el-date-picker
                                    :disabled="onlyShow"
                                    placeholder="选择日期"
                                    type="date"
                                    v-if="!see"
                                    v-model="form.effectiveTime"
                                    value-format="timestamp"
                            ></el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span class="dialog-footer" slot="footer" v-if="!onlyShow">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm('ruleForm')" size="small" type="primary">
          <i class="iconfont icon-save"></i>确 定
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        apiLawGet,
        apiLefDetailGet,
        apiPunishMatterAdd,
        apiPunishMatterGet,
        apiPunishRevise,
        apiPunishUpdate
    } from "../../../../api/case";
    import {formatDate} from "../../../../until/wordBook";

    export default {
        name: "edit",
        props: {
            show: {
                type: Boolean,
                default: false
            },
            lawId: {
                type: String,
                default: ""
            },
            onlyShow: {
                type: Boolean,
                default: false
            },
            tid:{
                type: String,
                default: ""
            },
            isRevise: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "编辑"
            },
            type: {
                type: String,
                default: ""
            },
            see: {
                type: Boolean,
                default: false
            }
        },
        data() {
            let checkDate = (rule, value, callback) => {
                if (!value && this.form.isEffective == 2) {
                    return callback(new Error("请选择生效日期"));
                } else {
                    callback();
                }
            };
            return {
                loading:true,
                form: {},
                typeList: [],
                enforceList: [],
                caseTypeList: [],
                rules: {
                    caseUseType: [
                        {required: true, message: "请选择适用案件类型", trigger: "change"}
                    ],
                    pmName: [
                        {required: true, message: "请输入处罚名称", trigger: "blur"}
                    ],
                    pmNum: [{required: true, message: "请输入处罚编号", trigger: "blur"}],
                    illegalTypes: [
                        {required: true, message: "请选择违法行为类型", trigger: "blur"}
                    ],
                    violationRegulations: [
                        {required: true, message: "请输入违反条例", trigger: "blur"}
                    ],
                    violationRegulationsContent: [
                        {required: true, message: "请输入违反条例内容", trigger: "blur"}
                    ],
                    accordingRegulations: [
                        {required: true, message: "请输入依据条例", trigger: "blur"}
                    ],
                    accordingRegulationsContent: [
                        {required: true, message: "请输入依据条例内容", trigger: "blur"}
                    ],
                    rightclause: [
                        {required: true, message: "请输入职权依据", trigger: "blur"}
                    ],
                    naturalRangeMin: [
                        {required: true, message: "请输入罚款下限", trigger: "blur"}
                    ],
                    naturalRangeMax: [
                        {required: true, message: "请输入罚款上限", trigger: "blur"}
                    ],
                    legalRangeMin: [
                        {required: true, message: "请输入罚款下限", trigger: "blur"}
                    ],
                    legalRangeMax: [
                        {required: true, message: "请输入罚款上限", trigger: "blur"}
                    ],
                    effectiveTime: [{validator: checkDate, trigger: "blur"}]
                }
            };
        },
        methods: {
            closeDialog() {
                this.$emit("update:show", false);
            },
            changeType(data) {
                this.form.illegalTypesName = this.typeList.find(i => {
                    return i.dictValue === data;
                }).dictName;
            },
            submitForm() {
                this.$refs.ruleForm.validate(valid => {
                    if (valid) {
                        this.form.caseUseType = this.form.caseUseType.join(",");
                        if (this.type) {
                            //修订
                            if (this.isRevise) {
                                apiPunishRevise(this.form).then(res => {
                                    this.closeDialog();
                                    this.$emit("getList");
                                    this.$message({
                                        type: "success",
                                        message: "修订成功!"
                                    });
                                });
                            } else {
                                //更新
                                apiPunishUpdate(this.form).then(res => {
                                    this.closeDialog();
                                    this.$emit("getList");
                                    this.$message({
                                        type: "success",
                                        message: "修改成功!"
                                    });
                                });
                            }
                        } else {
                            //添加
                            apiPunishMatterAdd(this.form).then(res => {
                                this.$emit("getList");
                                this.closeDialog();
                                this.$message({
                                    type: "success",
                                    message: "添加成功!"
                                });
                            });
                        }
                    }
                });
            },
            //弹框弹出时获取data给form
            initPage() {
                //案件类型字典
                this.$until.workBookApi("caseType").then(res => {
                    this.caseTypeList = res;
                });
                //get获取数据
                if (this.type) {
                    apiPunishMatterGet(this.type).then(res => {
                        this.loading=false;
                        this.form = res.data;
                        if (this.form.caseUseType) {
                            this.form.caseUseType = this.form.caseUseType.split(",")
                        }
                        apiLefDetailGet(res.data.enforceId).then(res => {
                            this.$until.workBookApi(res.data.enforceCode).then(res => {
                                this.typeList = res;
                            });
                        });
                    });
                } else {
                    this.loading=false;
                    apiLawGet(this.tid).then(res => {
                        apiLefDetailGet(res.data.enforceId).then(res => {
                            this.$until.workBookApi(res.data.enforceCode).then(res => {
                                this.typeList = res;
                            });
                        });

                        this.form = {
                            enforceName: res.data.enforceName,
                            lawId: res.data.lawId,
                            enforceId: res.data.enforceId,
                            lawName: res.data.lawName,
                            lawVersion: res.data.version,
                            pmName: "",
                            violationRegulations: "",
                            violationRegulationsContent: "",
                            accordingRegulations: "",
                            accordingRegulationsContent: "",
                            rightclause: "",
                            naturalRangeMin: 0,
                            legalRangeMin: 0,
                            legalRangeMax: 0,
                            naturalRangeMax: 0,
                            isWarning: "",
                            isChange: "",
                            isChangebefore: "",
                            isFine: "",
                            isRevokeGoods: "",
                            isConfiscate: "",
                            isStop: "",
                            isRevoke: "",
                            isForceDismantle: "",
                            isDedention: "",
                            isOther: "",
                            isOtherPunish: "",
                            isEffective: 1,
                            effectiveTime: "",
                            checkList: []
                        };
                    });
                }
            }
        },
        filters: {
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd hh:mm:ss");
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                    this.$nextTick(() => {
                        this.$refs.ruleForm.clearValidate();
                    });
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    fieldset {
        border: 1px solid #ccc;
        padding: 10px;
        border-radius: 8px;

        legend {
            text-align: center;
            padding: 0 5px;
            color: #aaa;
        }
    }

    .alert_wrop {
        padding: 10px;
        display: flex;
        color: red;
        justify-content: space-between;
    }

    .line {
        border-bottom: 1px solid #eee;
        margin: 5px 20px;
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica,
        "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: bold;
    }

    .more-select {
        width: 100%
    }
</style>


